import styled from 'styled-components';

export const Section = styled.section`
  min-height: 100vh;
`;

export const Hero = styled.div`
  padding: 100px;
  @media (max-width: 768px) {
    padding: 20px;
  }
  background: rgba(0, 0, 0, 0.3);
  border-radius: 20px;
`;



